<template lang="pug">
    v-list-item
        v-list-item-content
            
            v-list-item-title
                v-row
                    v-col(cols="12" sm="8" lg="9")
                        h3 {{expireData}} 
                    v-col(cols="12" sm="4" lg="3")
                        v-btn(color="success" dark @click="getmore") 更多
                
            v-row
                v-col(cols="12" sm="12" lg="3")
                    h3 From:{{fromusername}}
                    div(class="text-wrapper") {{from}}
                v-col(cols="12" sm="12" lg="3")
                    h3 To:{{tousername}}
                    div(class="text-wrapper") {{to}}
                v-col(cols="12" sm="12" lg="3")
                    h3 Amount:
                    div(class="text-wrapper") {{amount}}
                v-col(cols="12" sm="12" lg="3")
                    h3 Token:
                    div(class="text-wrapper") {{tokenname}}
                v-col(cols="12" sm="12" lg="6")
                    h3 Hash:
                    v-textarea(:value="hash" rows="2") 
                v-col(cols="12" sm="12" lg="6")
                    h3 Memo:
                    v-textarea(:value="memo" rows="2") 
                v-col(cols="12" sm="12" lg="12")
                    v-divider

        //- v-list-item-action
        //-     v-btn(color="success" dark @click="getmore") 更多
</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    name: "historylistitem",
    props:{
        time:{
            type:Number,
            default:0,
        },
        from:{
            type:String,
            default:"",
        },
        to:{
            type:String,
            default:"",
        },
        arrow:{
            type:String,
            default:"",
        },
        hash:{
            type:String,
            default:"",
        },
        tokenname:{
            type:String,
            default:"",
        },
        amount:{
            type:Number,
            default:0,
        }
    },
    data: () => ({
        memo:"",
        fromusername:"",
        tousername:"",
    }),
    computed: {
        expireData(){
            return new Date(this.time+3600*8000).toISOString().substr(0, 19)
        },
    },
    methods:{
        getmore(){
            api.GetMemoByHash({hash:this.hash}).then(res=>{
                if (res.code==200){
                    this.memo=res.data;
                }else{
                    this.memo=res.msg+res.error;
                }
            })
            api.GetUserNameByAddress({address:this.from}).then(res=>{
                if (res.code==200){
                    this.fromusername=res.data;
                }
            })
            api.GetUserNameByAddress({address:this.to}).then(res=>{
                if (res.code==200){
                    this.tousername=res.data;
                }
            })
        }
    },
    
}
</script>



<style lang="scss">

.text-wrapper {
    // overflow: hidden;
    -webkit-line-clamp: 2;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}
</style>